.container {
    position: relative;
  }
  
  .modalBackdrop {
    position: fixed;
    right: 20px;
    top: 0;
    left: 20px;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
  }
  
  .modalContent {
    background-color: #151515;
    border-radius: 1rem;
    padding: 1.5rem;
    max-width: 28rem;
    width: 90%;
    position: relative;
    box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  }
  
  .closeButton {
    position: absolute;
    top: 0.75rem;
    right: 0.75rem;
    background: none;
    border: none;
    color: #ec0000;
    cursor: pointer;
    transition: color 0.2s;
  }
  
  .closeButton:hover {
    color: #000000;
  }
  
  .modalTitle {
    font-size: 1.25rem;
    font-weight: 700;
    margin-bottom: 1rem;
    text-align: center;
    color: #00ff1e;
    text-shadow: 0 0 8px rgba(0, 255, 42, 0.8);
  }
  
  .modalText {
    color: #ffffff;
    margin-bottom: 1.5rem;
    text-align: center;
    text-shadow: 0 0 8px rgba(255, 255, 255, 0.8);
  }
  
  .actionButton {
    width: 100%;
    padding: 0.75rem;
    background-color: #00ff1e;
    color: rgb(0, 0, 0);
    border-radius: 0.75rem;
    font-weight: 600;
    font-size: 15px;
    border: none;
    cursor: pointer;
    transition: background-color 0.2s;
  }
  
  .actionButton:hover {
    background-color: #1f2937;
  }